import { Component, h, Prop, Host } from '@stencil/core';

@Component({
  tag: 'ks-order-address-section',
})
export class OrderAddressSection {
    @Prop() heading: string;
    @Prop() link: string;
    @Prop() editString: string;

    render() {
        return <Host>
            <ks-order-heading heading={this.heading} link={this.link}></ks-order-heading>
            <slot/>
        </Host>;
    }
}